.MuiCard-root{
  background-color: #4F179B;
}
.logosBox{
  position: relative;
  display: inline-block;
}
.logosBox img{
  width: auto;
  height: auto;
  max-height: 64px;
  max-width: 150px;
  vertical-align: middle;
  margin-top: 2rem;
}
/* PAGE */
.fontBold{
  font-weight: 700;
}
.subHeader::before{
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  height: 13vh;
  background-color: #5D1BB8;
  z-index: -1;
}
.subHeader{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.containerInfoSub{
  display: flex;
  flex-direction: column;
}
