.farmImg{
  margin-top: .5rem;
  vertical-align: top;
  z-index: 1;
  margin-bottom: .5rem;
  width: 100%;
  height: auto;
  max-width: 250px;
  max-height: 250px;
  margin-left: 0.5rem;
  z-index: 0;
}
.pageInfo{
  display: flex;
  flex-direction: column;
  padding: .5rem;
  padding-top: 0;
}
h1.titlePage{
    font-size: 3em;
}
.pageInfo span{
  font-size: 1.2em;
  color: #9D94AB;
}
.gridFlex{
  display: flex;
  flex-direction: column;  
}
.baseCard.singlFarmCard, .baseCard.farmInfo{
  background-color: #4F179B;
  border-radius: 2rem!important;
  padding: 1rem;
  border: 1px solid #6625BF;
  box-shadow: 0 0 0 0 white;
  transition: box-shadow .3s;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  z-index: 0;
}
.baseCard.farmInfo::after{
  content: "";
  position: absolute;
  background-image: url(../../assets/img/pdshares-final2.png);
  background-size: cover;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  opacity: .1;
}
.baseCard.farmInfo.genesisFarm::after{
  background-image: url(../../assets/img/pddollarc.png);
}
.iconAccordion{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.baseCard.singlFarmCard .iconAccordion::after{
  color:#BB24EC;
  content: "â–¼";
  transform-origin: center;
  transition: transform .3s;
  margin-top: 2rem;
}
.baseCard.singlFarmCard.Mui-expanded .iconAccordion::after{
  transform: rotateZ(180deg);
}
.baseCard:hover{
  box-shadow: 0 0 10px 0 #6625BF;
}
.baseCard.singlFarmCard{
  padding: 0;
}
.baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item{
  position: relative;
}
.baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item::after{
  content: "";
  width: 1px;
  height: 70px;
  position: absolute;
  background-color: #BB24EC;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: block;
}
.baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item:first-child:after{
  display: none;
}
.baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item:last-child:after, .baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item.stakeSingleFarm:after{
  display: none;
}
.add2Farm{
  position: relative;
}
.rewardSingleFarm, .stakeSingleFarm{
  position: relative;
  border-radius: .5rem;
  display: flex;
  flex-direction: column;
}
.titleLogoSingleFarm{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.accordionSummary{
  justify-content: space-between;
}
.reverse{
  flex-direction: column-reverse;
  margin-bottom: 2rem;
}
.titleInfoSingleFarm{
  position: absolute;
  top: 0;
  margin-top: .85rem;
  font-size: 1.2em;
  line-height: 1em;
  display: inline-block;
}
.containerInfoSingleFarm{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.singleFarmApr, .singleFarmTvl{
  color: #00FF6C;
  font-weight: 700;
  font-size: 1.5em;
  margin-top: 2rem;
}
.tokenCard.deposit, .claimReward{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  height: 100%;
  padding: 1rem;
  border-radius: 2rem;
  overflow: hidden;
  border: 1px solid #BB24EC;
  background-image: url('../../assets/img/reflect.svg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center left -500px;
  margin: 0;
}
.tokenCard.deposit:hover, .claimReward:hover{
  background-position: center right -500px;
  transition: background-position .6s ease-out;
}
.addRemoveButtons{
  display: flex;
  margin-bottom: 1rem;
  justify-content: space-around;
  width: 200px;
}
.removeFunds button, .addFunds button{
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: .8rem;
  border: 0;
  line-height: 2em;
  height: auto;
  padding: .5rem;
  padding-top: .4rem;
  padding-bottom: 0;
  font-weight: 400;
  font-size: inherit;
  font-size: .55em;
  text-transform: none;
  min-width: 70px;
}
.removeFunds button{
  color: white;
  background-color: red;
}
.addFunds button{
  color: white;
  background-color: #40AC6E;
}
.removeFunds button:disabled{
  color: white;
  background-color: red;
  opacity: .5;
}
.removeFunds button::before{
  content: "-";
  font-size: 5em;
  position: relative;
  display: block;
}
.addFunds button::before{
  content: "+";
  font-size: 5em;
  position: relative;
  display: block;
}
.removeFunds button:hover{
  color: white;
  opacity: .5;
  background-color: red;
}
.addFunds button:hover{
  color: white;
  opacity: .5;
  background-color: #40AC6E;
}
.tokenCard.deposit .label, .claimReward .label{
  position: absolute;
  left: 0;
  top: 0;
  background-color: #FFD148;
  padding: 1rem 1rem .5rem 1.5rem;
  color: black;
  font-size: 1.2em;
  text-transform: uppercase;
}
.claimReward .label.pinkLabel{
  color: white;
  background-color: #BB24EC;
}
.MY{
  margin-top: .5rem;
  margin-bottom: .5rem;
}
.MY2{
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
.MB{
  margin-bottom: .5rem;
}
.MB2{
  margin-bottom: 1.5rem;
}
.MT{
  margin-top: .5rem;
}
.MT2{
  margin-top: 1.5rem;
}
.farmEarn{
  font-size: 2em;
  color: #BB24EC;
  font-weight: 700;
  line-height: 1em;
  z-index: 1;
}
.farmLiveEnd{
  font-size: 1.2em;
  line-height: 1em;
  margin-top: .5rem;
  z-index: 1;
}
.farmEndDate{
  color: white;
  background-color: #BB24EC;
  line-height: 1.5em;
  display: block;
  z-index: 1;
  font-weight: 700;
  text-align: center;
  padding: .5rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  border-radius: 1rem;
}
.farmLiveInfo{
  color: #9D94AB;
  line-height: 1em;
  z-index: 1;
}
.genesisFarm .farmEarn{
  color: #FFD148;
}
.genesisFarm .farmEndDate{
  background-color: #FFD148;
  color: black;
}
.endStaking, .addLiquidityPair{
  padding: 1rem;
}
.claimAddLiquidity{
  display: flex;
  flex-direction: column;
}
a.buttonAddLiquidity{
  color: black;
  text-decoration: none;
}
.POLYDOGEclass{
  display: none;
}
@media screen and (min-width: 768px){
  .subHeader{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
  .titleLogoSingleFarm{
    display: block;
    align-items: unset;
    justify-content: unset;
  }
  /* .boardRoomLive{
    align-self: center;
    margin-top: 0;
  } */
  .farmImg{
    margin-left: unset;
  }
  .logosBox{
    display:block;
  }
  .logosBox img{
    vertical-align: unset;
  }
  .baseCard.singlFarmCard .MuiGrid-container .MuiGrid-item:first-child:after{
    display: block;
  }
  .containerInfoSub{
    flex-direction: row;
  }
  .pageInfo{
    padding-top: 3rem;
  }
  .claimAddLiquidity{
    flex-direction: row;
  }
}
/* END PAGE */
@media screen and (min-width:960px){
  .reverse{
    flex-direction:row;
  }
  .infoFarm{
    display: flex;
  }
}